* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

$nav-height: 50px;

body {
  font-family: $font1;
}
.logo {
  width: 16%;
  height: $nav-height;
  display: flex;
  float: left;
  align-items: center;
  @media (max-width: 1199px) {
    width: 16%;
  }
  @media (max-width: 767px) {
    width: 24%;
  }
  @media (max-width: 575px) {
    width: 31%;
  }
  &--expand {
    @media (max-width: 1199px) {
    }
    @media (max-width: 767px) {
    }
  }
  a {
    float: left;
  }
  &__img {
    max-height: $nav-height - 20px;
    // padding: 0 15px;
    &--expand {
      width: 40%;
      margin: auto 0;
    }
  }
}
.menu {
  width: 84%;
  height: $nav-height;
  float: left;
  position: relative;
  display: flex;
  align-items: center;
  @media (max-width: 1199px) {
    width: 84%;
  }
  @media (max-width: 767px) {
    width: 76%;
  }
  @media (max-width: 575px) {
    width: 69%;
  }
  &--expand {
    width: 94%;
  }
  &__nav {
    @include resetList();
    &--restyle {
      margin: auto 0;
      position: absolute;
      right: 0;
      @media (max-width: 575px) {
        // position: unset;
        right: 15px;
      }
    }
    .hamb {
      padding-right: 15px;
      @media (min-width: 768px) {
        display: none;
      }
      @media (max-width: 575px) {
        // position: absolute;
        width: 38px;
        height: 100%;
        // right: 10px;
      }
    }
  }
  &__dropdown {
    &--restyle {
      width: 300px;
      max-height: 500px;
      overflow: auto;
      margin: 0 -231px 0;
      padding: 0;
      border-radius: 0px;
      box-shadow: 2px 2px 7px $sliver-chalice-color;
      &--user {
        display: none;
        width: 200px;
        top: 48px;
        box-shadow: 2px 2px 5px $sliver-chalice-color;
        border-radius: 3px;
        @media (max-width: 767px) {
          position: absolute !important;
          left: 58px;
        }
        @media (max-width: 575px) {
          left: -87px;
        }
        a {
          color: $sliver-chalice-color;
          &:hover {
            background-color: $second-light-color;
          }
        }
        i {
          color: $orange-color;
          margin: 0 10px;
          font-weight: bold;
        }
      }
    }
  }
  &__item {
    padding: 0 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    float: left;
    &--inline {
      display: inline-block;
    }
  }
  &__wraper {
    height: 50px;
    width: 100%;
    .navbar--restyle {
      height: 50px;
      padding: 0 0;
      background-color: $background-menu;
      border-bottom: 1px solid transparentize($color: $trout-color, $amount: 0.8);
    }
  }
}
.user {
  &__name {
    color: $apple-green-color;
    font-weight: 600;
    padding: 3px;
    &:hover {
      color: $feijoa-color;
    }
    @media (max-width: 575px) {
      display: none;
    }
    &--res {
      display: block;
      text-align: center;
      color: $orange-color;
      font-weight: 600;
      padding: 0;
      @media (min-width: 767px) {
        display: none;
      }
    }
  }
  &__img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    margin: auto 10px;
    &--position {
      // @media (max-width: 575px) {
      //   margin-left: 18.5rem;
      // }
      // @media (max-width: 530px) {
      //   margin-left: 16.65rem;
      // }
      // @media (max-width: 480px) {
      //   margin-left: 14.5rem;
      // }
      // @media (max-width: 320px) {
      //   margin-left: 7.5rem;
      // }
    }
  }
  &:hover {
    color: $dark-color;
  }
}
