$status-success-color: #28a745;
$status-pending-color: #e7af04;
$status-error-color: #dc3545;
$status-text-color: white;

.status {
  display: inline-block;
  font-size: 13px;
  text-align: center;
  padding: 3px;
  color: $status-text-color;
  vertical-align: text-top;
  min-width: 55px;
  border-radius: 3px;
  cursor: default;
  &--icon {
    min-width: initial;
    padding: 0;
    width: 25px;
    height: 28px; 
    line-height: 28px;
  }
  &--success {
    background-color: $status-success-color;
  }
  &--pending {
    background-color: $status-pending-color;
  }
  &--error {
    background-color: $status-error-color;
  }
}
