.chart {
    &__text--td:hover {
        text-decoration: none;
    }
    &__resp--mt {
        @media (max-width: 767px) {
            margin-top: 1.5em;
        }
        @media (max-width: 575px) {
            margin-top: 1.5em;
        }
    }
    .list {
        padding-left: 0;
        padding-right: 0;
        &__list-item {
            padding: .75rem .5rem;
            position: relative;
            & .c-red {
                color: rgba(255, 99, 132, 0.2),
            }
            & .c-yellow {
                color: rgba(255, 206, 86, 0.2);
            }
            & .c-blue {
                color: rgba(54, 162, 235, 0.2);
            }
        }
        &__body {
            margin-left: .5em;
            margin-right: .5em;
            display: inline-flex;
        }
        &__body:after {
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            border-bottom: 1px solid rgba(120, 130, 140, .065);
            bottom: 0;
            max-width: 100%;
        }
    }
}
