$base-px-value: 5px;

.hidden {
  display: none;
}

.wrap {
  display: inline-block;
}
/**
 * Value = +5 each
 */
@for $i from 1 through 10 {
  .space--top-#{$i} {
    margin-top: $i * $base-px-value;
  }
  .space--bottom-#{$i} {
    margin-bottom: $i * $base-px-value;
  }
  .space--left-#{$i} {
    margin-left: $i * $base-px-value;
  }
  .space--right-#{$i} {
    margin-right: $i * $base-px-value;
  }
  .space--all-#{$i} {
    margin: $i * $base-px-value;
  }
  .space__inner--top-#{$i} {
    padding-top: $i * $base-px-value;
  }
  .space__inner--bottom-#{$i} {
    padding-bottom: $i * $base-px-value;
  }
  .space__inner--left-#{$i} {
    padding-left: $i * $base-px-value;
  }
  .space__inner--right-#{$i} {
    padding-right: $i * $base-px-value;
  }
  .space__inner--all-#{$i} {
    padding: $i * $base-px-value;
  }
}
.space--auto {
  margin: auto;
}

// display type
.block {
  display: block;
}
.flex {
  display: flex;
}


.no-wrap {
  white-space: nowrap;
}
