    width: 15px;
    border-radius: 0px; }
    .checkbox--sm .checkbox__label:after {
      content: "";
      height: 7.5px;
      width: 100%;
      border: 3.33333px solid transparent;
      border-top: 0;
      border-right: 0;
      border-color: transparent;
      position: absolute;
      transform: rotate(-45deg); }
  .checkbox--sm:hover .checkbox__label:after {
    content: "";
    height: 7.5px;
    width: 100%;
    border: 3.33333px solid transparent;
    border-top: 0;
    border-right: 0;
    border-color: rgba(255, 255, 255, 0.5);
