.email {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  &__container {
    cursor: pointer;
    position: relative;
    opacity: 0;
    top: 100px;
    -webkit-transition: all 0.15s ease-in-out;
    -moz-transition: all 0.15s ease-in-out;
    -o-transition: all 0.15s ease-in-out;
    -ms-transition: all 0.15s ease-in-out;
    transition: all 0.15s ease-in-out;
  }
  &__item {
    width: 100%;
    margin: 5px;
    float: left;
    padding: 0px;
    position: relative;
    overflow: hidden;
    &:hover {
      .email__container {
        opacity: 1;
        transform: translateY(-100px);
        -webkit-transform: translateY(-100px);
        -moz-transform: translateY(-100px);
        -ms-transform: tzzzranslateY(-100px);
        -o-transform: translateY(-100px);
      }
    }
  }
  &__img {
    width: 100%;
    height: auto;
    margin: 0px;
    padding: 0px;
    float: left;
    z-index: 4;
  }
  &__blur {
    background-color: rgba(0, 0, 0, 0.7);
    height: 100vh;
    width: 100%;
    z-index: 5;
    position: absolute;
  }
  &__text {
    z-index: 10;
    color: #fff;
    position: absolute;
    width: 100%;
    text-align: center;
    margin: 30% 0 30% 0;
    &__title {
      text-transform: uppercase;
      font-size: 18px;
      @media screen and (max-width: 426px) {
        text-transform: uppercase;
        font-size: 10px;
      }
    }
    &__btn {
      @media screen and (max-width: 426px) {
        text-transform: uppercase;
        font-size: 5px;
      }
    }
  }
  &__margin {
    &--mtrl5 {
      margin: 5px 5px 0px 5px;
    }
    &--mrbl5 {
      margin: 0px 5px 5px 5px;
    }
  }
  &__description--w100 {
    width: 100%;
  }
  &__inputtext--w100 {
    width: 100%;
  }
}
