.custom {
   position: fixed;
   display: flex;
   bottom: 0;
   right: 0;
    &__social-icon a {
        color: #FFF;
        padding: 10px 0px 10px 18px;
        background-color: #508a2d;
        i {
            padding-right: 13px;
        }
        &:hover {
            text-decoration: none;
        }
    }
    &__contact-us a {
        color: #FFF;
        padding: 10px 24px 10px 24px;
        background-color: #67ae3d;
        &:hover {
            text-decoration: none;
        }
    }
}