
.log {
  // border: 3px solid $feijoa-color;
  // background-color: $feijoa-color;
  overflow-y: scroll;
  max-height: 400px;
  padding-right: 5px;
  &::-webkit-scrollbar-track
  {
    // -webkit-box-shadow: inset 0 0 6px $dark-color;
    background-color: $background-menu;
    border: 1px solid $light-color;
    border-radius: 10px;
  }
  
  &::-webkit-scrollbar
  {
    width: 10px;
    // background-color: $light-color;
  }
  
  &::-webkit-scrollbar-thumb
  {
    border-radius: 10px;
    background-color: $light-color;
  }

  &__item{
    background-color: $background-menu;
    border: 3px solid $light-color;
    padding: 15px;
  }

  &__activity {
    text-align: justify;
  }

  &__time {
    color: $main-text-color;
    font-size: 14px;
    font-style: italic;
    padding: 2px 5px;
    font-weight: 600;
    background-color: $light-color-1;
  }

  &__images {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  &__image-link {
    display: block;
    min-width: 12.5%;
    max-width: 25%;
    flex-basis: 25%;
  }

  &__image {
  }
}
