.fieldset-group {
  margin-top: 1rem;
  &__legend {
    font-size: 0.99rem;
    display: block;
    margin-bottom: 1rem;
    position: relative;
    color: #047bf8;
    &:before {
      content: "";
      position: absolute;
      left: 0px;
      right: 0px;
      height: 1px;
      top: 50%;
      background-color: rgba(0, 0, 0, 0.1);
      z-index: 1;
    }
  }
  &__title {
    padding: 0px 0.5rem 0 0;
    background-color: #fff;
    display: inline-block;
    z-index: 2;
    position: relative;
  }
}
