// Checkbox
//
// Checkbox style
//
// Markup:
// <div class="checkbox">
//   <input type="checkbox" class="checkbox__input" id="testCheckbox">
//   <label for="testCheckbox" class="checkbox__label {{modifier_class}}"></label>
// </div>
//
// .checkbox__label--carnation - carnation
// .checkbox__label--feijoa - feijoa
// .checkbox__label--jungle - jungle
// 
// Styleguide Components.Checkbox
.checkbox {
  $b: &;
  display: inline-block;
  vertical-align: middle;

  &__label {
    cursor: pointer;
    border: $checkbox-border-w solid $checkbox-bg-default;
    margin: 0;
    position: relative;
    display: block;
    @include checkbox-size($checkbox-w, $checkbox-border-radius);
    &:hover {
      &:after {
        @include checkbox-icon($checkbox-w, transparentize(color-yiq($checkbox-bg-default), 0.5));
      }
    }
  }
  
  &__input {
    display: none;
    &:checked + #{$b}__label {
      &:after {
        @include checkbox-icon($checkbox-w, color-yiq($checkbox-bg-default));
      }
    }
  }

  &--sm {
    #{$b}__label {
      cursor: pointer;
      border: $checkbox-border-w solid transparent;
      margin: 0;
      position: relative;
      display: block;
      @include checkbox-size($checkbox-sm-w, $checkbox-border-radius);
      &:after {
        @include checkbox-icon($checkbox-sm-w, transparent);
      }
    }

    &:hover {
      #{$b}__label {
        &:after {
          @include checkbox-icon($checkbox-sm-w, transparentize(#fff, 0.5));
        }
      }
    }
  }
}



@each $color, $value in $checkbox-colors {
  .checkbox--#{$color} {
    .checkbox__input:checked + .checkbox__label:after{
      border-color: color-yiq($value);
    }
    .checkbox__label:after {
      border-color: transparentize($color: color-yiq($value), $amount: 0.5);
    }
    .checkbox__label {
      border-color: $value;
    }
  }
}
