$fixed-offset-r: 15px;
$fixed-offset-b: 15px;
$fixed-cricle-d: 50px;
$fixed-icon-size: 23px;
$fixed-btn-bg: $main-color;
$fixed-icon-cl: white;

.fixed {
  position: fixed;
  bottom: $fixed-offset-b;
  right: $fixed-offset-r;
  &__btn {
    width: $fixed-cricle-d;
    height: $fixed-cricle-d;
    text-align: center;
    border-radius: 50%;
    border: none;
    cursor: pointer;
    background-color: transparentize($color: $fixed-btn-bg, $amount: 0.2);
    transition: .3s background-color;
    &:hover {
      background-color: $fixed-btn-bg;
    }
  }
  &__btn-icon {
    line-height: $fixed-cricle-d;
    font-size: $fixed-icon-size;
    color: $fixed-icon-cl;
    animation: 1s ease-in 0s reverse infinite swing;
  }
}
