@mixin checkbox-variant($bg, $border-color, $hover-bg: darken($bg, 7.5%), $hover-border: darken($border-color, 10%)) {
  background-color: $bg;
  border-color: $border-color;
  &:hover {
    background-color: $hover-bg;
    border-color: $hover-border;
  }
}

@mixin checkbox-size($width, $border-radius) {
  height: $width;
  width: $width;
  border-radius: $border-radius;
}

@mixin checkbox-icon($checkbox-w, $bg) {
  content: "";
  height: $checkbox-w / 2;
  width: 100%;
  border: $checkbox-w/4.5 solid transparent;
  border-top: 0; 
  border-right: 0;
  border-color: $bg;
  position: absolute;
  transform: rotate(-45deg);
}
