.vue-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  overflow-y: auto;
  pointer-events: none;
  &--active {
    background-color: rgba($color: #000000, $alpha: 0.2);
    pointer-events: initial;
  }
  &__box {
    background: white;
    top: 50%;
    left: 50%;
    z-index: 1;
    width: 40%;
    min-width: 500px;
    margin-top: 100px;
    margin-bottom: 100px;
    @media (max-width: 575px) {
      min-width: 320px;
    }
  }
  &__close {
    position: absolute;
    right: 0;
    width: 50px;
    text-align: center;
    height: 100%;
    border-left: 1px solid #ccc;
    top: 0;
    line-height: 47px;
    cursor: pointer;
  }
  &__head {
    padding: 0.5em;
    border-bottom: 1px solid #ccc;
    font-size: 1.25em;
    position: relative;
  }
  &__body {
    min-height: 200px;
    padding: 0.5em;    
  }
  &__footer {
    padding: 0.5em;    
  }
}
