// Forms Control
//
// Forms, [readmore](https://getbootstrap.com/docs/4.0/components/forms/#form-controls)
//
// Markup:
// <div class="form-group">
//   <label for="exampleInputPassword1">Password</label>
//   <input type="password" class="form-control {{modifier_class}}" id="exampleInputPassword1" placeholder="Password">
// </div>
//
// .form-control--darkshadow - boxshadow-dark.
//
// Styleguide Components.Forms.Control

.form-control {
  &--darkshadow {
    &:focus {
      border-color: rgb(104, 145, 162);
      outline: 0;
      -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgb(104, 145, 162);
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgb(104, 145, 162);
    }
  }
}
