// Card
// 
// Card description [Readmore](https://getbootstrap.com/docs/4.0/components/button-group/)
//
// Markup:
// <div class="row p-5">
//   <div class="col-sm-3">
//     <div class="card {{modifier_class}}">
//       <div class="card__body">
//         <div class="card__header">
//           <div class="text-primary">
//             <i class="fa fa-hospital-o" aria-hidden="true"></i>
//           </div>
//           <div class="highlight-text">
//             <p class="card__title">Bnh Vin</p>
//             <p class="card__number">600</p>
//           </div>
//         </div>
//         <p class="card__info">
//           <i class="fa fa-bookmark-o text-warning" aria-hidden="true"></i>
//           Lorem ipsum dolor sit amet.
//         </p>
//       </div>
//     </div>
//   </div>
// </div>
//
// .card--layer-1 - Card layer box shadow 1
// .card--layer-2 - Card layer box shadow 2
// .card--layer-3 - Card layer box shadow 3
// .card--layer-4 - Card layer box shadow 4
// .card--layer-5 - Card layer box shadow 5
//
// Styleguide Components.Card
.card {
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.2);
  &__head {

  }

  &__body--outline {
    border: 1px solid ;
  }

  @for $i from 1 through 5 {
    &--layer-#{$i} {
      box-shadow: 0 0 ($i * 3px) ($i * 0.5px) rgba(0, 0, 0, 0.2);
    }
  }

  &__body {
    background-color: #fff;
    padding: 15px;
  }
  &__header {
    background: #f4f4f4;
    display: flex;
    justify-content: space-between;
    padding: 15px;
    .text-primary>svg {
      fill: $title-text-color;
      width: 70px;
      height: 70px;
      margin: 10px 25px;
      color: $sliver-chalice-color;
      @media (max-width: 1199px) {
        margin: 10px;
        width: 65px;
        height: 65px;
      }
      @media (max-width: 1024px) {
        margin: 5px;
        width: 60px;
        height: 60px;
      }
      @media (max-width: 767px) {
        margin: 5px;
        width: 50px;
        height: 50px;
      }
      @media (max-width: 575px) {
        margin: 5px 20px 5px 0;
        width: 60px;
        height: 60px;
      }
    }
  }
  &__title {
    font-family: $font1;
    color: $title-text-color;
    font-weight: 700;
    font-size: 20px;
    margin-bottom: 0;
    @media (max-width: 1199px) {
      font-size: 19px;
    }
    @media (max-width: 1024px) {
      font-size: 19px;
    }
    @media (max-width: 767px) {
      font-size: 15px;
    }
    @media (max-width: 575px) {
      font-size: 18px;
    }
    &--up {
      text-transform: uppercase;
    }
  }
  &__number {
    color: $apple-green-color;
    font-size: 50px;
    margin-bottom: 0;
    text-align: center;
    font-weight: bold;
    @media (max-width: 1199px) {
      font-size: 42px;
    }
    @media (max-width: 1024px) {
      font-size: 35px;
    }
    @media (max-width: 767px) {
      font-size: 35px;
    }
    @media (max-width: 575px) {
      font-size: 40px;
    }
  }
  &__info {
    margin-top: 15px;
    margin-bottom: 0;
    text-align: center;
    color: #868e96 !important;
    @media (max-width: 1199px) {
      font-size: 14px;
    }
    i {
      margin-right: 0.25em;
    }
  }
}
