// Typography
//
// All Heading
//
// Markup:
// <h1> Lorem ipsum is a pseudo-Latin text used in web design <h1>
// <h2> Lorem ipsum is a pseudo-Latin text used in web design <h2>
// <h3> Lorem ipsum is a pseudo-Latin text used in web design <h3>
// <h4> Lorem ipsum is a pseudo-Latin text used in web design <h4>
// <h5> Lorem ipsum is a pseudo-Latin text used in web design <h5>
// <h6> Lorem ipsum is a pseudo-Latin text used in web design <h6>
// 
// Styleguide Settings.Typography
body {
  font-family: 'Open Sans', sans-serif;
}

h1 {
  font-size: $s1-heading;
}

h2 {
  font-size: $s2-heading;  
}

h3 {
  font-size: $s3-heading;    
}

h4 {
  font-size: $s4-heading;   
}

h5 {
  font-size: $s5-heading;   
}

h6 {
  font-size: $s6-heading;   
}

// Font Size
//
// All Heading
//
// Markup:
// <p class="{{modifier_class}}"> Lorem ipsum is a pseudo-Latin text used in web design <p>
// 
// .s1-heading - Font size heading 1.
// .s2-heading - Font size heading 2.
// .s3-heading - Font size heading 3.
// .s4-heading - Font size heading 4.
// .s5-heading - Font size heading 5.
// .s6-heading - Font size heading 6.
//
// Styleguide Settings.Typography.Size
.s1-heading {
  font-size: $s1-heading;
}

.s2-heading {
  font-size: $s2-heading;
}

.s3-heading {
  font-size: $s3-heading;
}

.s4-heading {
  font-size: $s4-heading;
}

.s5-heading {
  font-size: $s5-heading;
}

.s6-heading {
  font-size: $s6-heading;
}
