.page-main-login {
  background-color: rgba(4, 40, 68, 0.85);
  height: 100vh; //Hin th  cao full mn hnh 
  display: flex;
  justify-content: center;
  align-items: center;
}
.title {
  font-size: 25px;
  font-weight: 600;
}
.login {
  border-radius: 5px 5px 5px 5px;
  padding: 10px 20px 20px 20px;
  width: 90%;
  max-width: 500px;
  background: #ffffff;
  position: relative;
  margin: auto auto;
  box-shadow: 0px 1px 5px rgba(0,0,0,0.3);
  &__forgotpassword {
    color: #7AB893;
    text-align: left;
    &--content {
      margin-bottom: 5px;
      margin-top: 5px;
      width: 30%
    }
    &:hover {
      text-decoration: none;
      color: black;
    } 
  }
  &__input {
    &--content {
      padding: 10px 10px;
      border-radius: 5px;
      width: 100%;
      display: block;
      margin-bottom: 10px;
      z-index: 1;
      position: relative;
      -moz-box-sizing: border-box;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      transition: 0.2s all;
      &:focus {
        border-color: rgb(104, 145, 162);
        outline: 0;
        -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgb(104, 145, 162);
        box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgb(104, 145, 162);
      }
    }
  }
  &__logo {
    margin-bottom: 20px;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__btn {
    border-radius: 5px;
    transition: all 0.5s;
    cursor: pointer;
    //hiu ng load 
    .spinner {
      display: block;
      width: 40px;
      height: 40px;
      position: absolute;
      border: 4px solid #6EAA27;
      border-top-color: rgba(255,255,255,0.3);
      border-radius: 100%;
      left: 50%;
      top: 0;
      opacity: 0;
      margin-left: -20px;
      margin-top: -20px;
      animation: spinner 0.6s infinite linear;
      transition: top 0.3s 0.3s ease,
                opacity 0.3s 0.3s ease,
                border-radius 0.3s ease;
      box-shadow: 0px 1px 0px rgba(0,0,0,0.2);
    }
  }
  &.loading {
    .login__btn {
      max-height: 100%;
      .spinner {
        opacity: 1;
        top: 40%;
      }
    }  
  }
  
  &.ok {
    .login__btn {
      background-color: #8bc34a;
      .spinner{
        border-radius: 0;
        border-top-color: transparent;
        border-right-color: transparent;
        height: 20px;
        animation: none;
        transform: rotateZ(-45deg);
      }
    }
  }
}
@keyframes spinner {
  0% { transform: rotateZ(0deg); }
  100% { transform: rotateZ(359deg); }
}
