.main__map {
  height: 378px;
  @media (max-width: 1199px) {
    height: 346px;
  }
  @media (max-width: 1024px) {
    height: 400px;
  }
}
.find__map {
  height: 87.9vh;
  width: 100%;
}
.contact-us__map {
  height: 40vh;
}
.map {
  height: 100%;
}
@for $i from 0 through 100 {
  .map__border-#{$i} {
    border: 0px + $i solid #fff;
  }
}