.customer {
  &__label {
    margin: 20px 0 2px;
    color: #7f8fa4;
    font-weight: 400;
    font-size: 22px;
    &--textarea {
      margin: 18px 0 2px;
      color: #7f8fa4;
      font-weight: 400;
      font-size: 22px;
    }
  }
  &__padding {
    padding: 0 10px 0 0;
  }
  &__input {
    opacity: 0.8;
    padding: 10px 10px;
    border-radius: 5px;
    width: 100%;
    display: block;
    margin-bottom: 10px;
    z-index: 1;
    position: relative;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    transition: 0.2s all;
    // &:focus {
    //   border-color: rgb(104, 145, 162);
    //   outline: 0;
    //   -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgb(104, 145, 162);
    //   box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgb(104, 145, 162);
    // }
  }
  &__btn {
    border-radius: 5px;
    transition: all 0.5s;
    cursor: pointer;
  }
}
