.page-main {
  display: flex;
}
.main {
  width: calc(100% - 220px);
  padding-top: 15px;
  transition: .2s width;
  padding-bottom: 30px;
  @media (max-width: 1199px) {
    width: calc(100% - 200px);
  }
  @media (max-width: 767px) {
    width: 100%;
  }
  &__title {
    font-weight: 600;
    margin-bottom: 30px;
  }
}
