//
// .table-dark - with light text on dark backgrounds
// .table-striped -  to add zebra-striping to any table row within the
// .table--result - style table red
// .table--error - style table error
// .table--sm -style table sm, font-size 12px
// Styleguide Components.Tables
.table {
  th, td {
    border-top: 0;
    font-size: 14px;
  }
  &--result {
    background: #ccc;
    color: red;
  }
  thead tr .fix-nhanvien {
    width: 15%;
  }
  &--error {
    border: 5px solid #e9e9e9;
    box-shadow: rgba(0, 0, 0, 0.75);
  }
  @media (max-width: 767px) {
    th, td {
      border-top: 0;
      font-size: 12px;
    }
  }
}
