.contact-us {
  &__info {
  }
  &__title {
    text-align: center;
    color: #67ae3d;
  }
  &__intro {
    display: block;
    text-align: justify;
    border-bottom: 1px solid $sliver-chalice-color;
  }
  &__detail {
    display: flex;
  }
  &__icon {
    color: #67ae3d;
    margin: auto 10px auto 0;
    font-size: 22px;
    width: 22px;
  }
  &__text {
    margin: auto 0;
    font-size: 20px;
  }
  &__link {
    font-size: 40px;
    color: $title-text-color;
  }
  .fb:hover {
    color: $facebook-color;
  }
  .yt:hover {
    color: $youtube-color;
  }
  &__form {
    label {
      font-size: 22px;
    }
    span {
      color: #f00;
    }
  }

}